// Tooltips 
$(function () {
    $('[data-toggle="tooltip"]').tooltip()
    $('.toast').toast()
})


// btn__top
$(document).ready(function(){
    $('.btn__top').click(function(){
        $('body, html').animate({
            scrollTop: '0px'
        }, 300);
    });
    $(window).scroll(function(){
        if($(this).scrollTop() > 0 ){
            $('.btn__top').slideDown(300);
        } else {
            $('.btn__top').slideUp(500);
        }
    });
});

